/*
 *这是单独为hello mui准备的个性化css，可以覆盖标准mui的css定义；
 * 在实际项目开发时，建议为App单独写一个css文件，从而实现项目的自定义皮肤功能；
 *
 * */
@import "module/color";
@import "module/fn";

html, body {
	height: 100%;
	font-family: 'Hiragino Sans GB' , 'Microsoft YaHei UI', 'Simsun', sans-serif;
	background-color: $background-red;
}

a {
	color: $font-black;
	&:hover {
		text-decoration: none;
	}
}

.mui-bar{
	-webkit-box-shadow: none;
	box-shadow: none;
	.mui-title{
		text-transform: uppercase;
	}
}


.mui-switch-red.mui-active {
	border: 2px solid $red;
	background-color: $red;
}

.switchLanguage.mui-switch.mui-active:before {
	content: '中文';
}

.switchLanguage.mui-switch:before {
	content: 'Eng';
}

.mui-content {
	background-color: transparent;
}


.w700{
	width: p2m(700) !important;
	margin: 0 auto ;
}
.bgWhite{
	background-color: #fff !important;
}
.pad40{
	padding: p2m(40);
}


$topBarHeight: p2m(148);

.mui-bar.mui-bar-nav {
	background-color: $red;
	* {
		color: #fff;
	}
	
	//height: $topBarHeight;
}

.mui-bar-nav ~ .mui-content {
	//padding-top: $topBarHeight;
}

.padRed {
	display: inline-block;
	width: p2m(4);
	height: 1em;
	background-color: $red;
	vertical-align: middle;
	margin-right: 5px;
}

.lyuSearchWrap {
	padding-top: 10px;
	background: {
		image: url("/img/bg-searchBar.png");
		size: p2m(750) p2m(129);
		repeat: no-repeat;
		position: center 0;
	}
	
	.lyuSearchBar {
		width: p2m(700) !important;
		margin: 0 auto;
		
		//position: absolute;
		//top: p2m(300);
		
		input[type='search'] {
			background-color: #fff;
			box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.1);
			height: 40px;
		}
		
		.mui-placeholder {
			top: 4px;
		}
		.mui-input-row.mui-search .mui-icon-clear {
			top: 11px;
		}
	}
}


#opgAjaxLoading {
	position: fixed;
	z-index: 50000000;
	background-color: rgba(255, 255, 255, 0);
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	display: -webkit-box;
	-webkit-box-pack: center;
	-webkit-box-align: center;
	
}


.mask{
	position: fixed;
	z-index: 1001;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0,0,0,0.3);
	display:-webkit-box;
	-webkit-box-pack:center;
	-webkit-box-align:center;
}

.mui-plus {
	header.mui-bar {
		display: none;
		background: $red;
	}
	.mui-bar-nav {
		~ .mui-content {
			padding: 0;
		}
	}
}

/*hm开头的表示仅为 Hello MUI示例定义*/
.hm-description {
	margin: .5em 0;
}

.hm-description > li {
	font-size: 14px;
	color: #8f8f94;
}
